/* HotlistEntriesManager 主组件样式 */
.hotlist-entries-manager {
  background: rgba(255, 255, 255, 0.8);
  border-radius: 16px;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.manager-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background: rgba(255, 255, 255, 0.6);
}

.manager-header h2 {
  margin: 0;
  font-size: 20px;
  font-weight: 600;
  color: #1d1d1f;
}

.header-actions {
  display: flex;
  gap: 12px;
}

.refresh-btn {
  padding: 8px 16px;
  background: #007aff;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.refresh-btn:hover:not(:disabled) {
  background: #0051d5;
  transform: translateY(-1px);
}

.refresh-btn:disabled {
  background: #cccccc;
  cursor: not-allowed;
  transform: none;
}

.manager-content {
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .hotlist-entries-manager {
    margin: 0 16px;
    border-radius: 12px;
  }
  
  .manager-header {
    padding: 16px 20px;
    flex-direction: column;
    gap: 12px;
    align-items: flex-start;
  }
  
  .manager-content {
    padding: 20px;
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .manager-header h2 {
    font-size: 18px;
  }
  
  .header-actions {
    width: 100%;
    justify-content: flex-end;
  }
  
  .refresh-btn {
    padding: 6px 12px;
    font-size: 13px;
  }
}