/* HotlistEntriesFilters 筛选组件样式 */
.hotlist-entries-filters {
  background: rgba(255, 255, 255, 0.6);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  overflow: hidden;
}

.filters-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background: rgba(255, 255, 255, 0.4);
}

.filters-header h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
  color: #1d1d1f;
}

.reset-btn {
  padding: 6px 12px;
  background: transparent;
  color: #007aff;
  border: 1px solid #007aff;
  border-radius: 6px;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.reset-btn:hover {
  background: #007aff;
  color: white;
  transform: translateY(-1px);
}

.filters-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
  padding: 20px;
}

.filter-item {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.filter-item-wide {
  grid-column: 1 / -1;
}

.filter-label {
  font-size: 13px;
  font-weight: 600;
  color: #333;
  margin-bottom: 4px;
}

.filter-input,
.filter-select {
  padding: 10px 12px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  font-size: 14px;
  background: white;
  color: #333;
  transition: all 0.2s ease;
  outline: none;
}

.filter-input:focus,
.filter-select:focus {
  border-color: #007aff;
  box-shadow: 0 0 0 3px rgba(0, 122, 255, 0.1);
  transform: translateY(-1px);
}

.filter-input::placeholder {
  color: #999;
}

.filter-select {
  cursor: pointer;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 12px center;
  background-size: 16px;
  padding-right: 40px;
}

.filter-select:hover {
  border-color: rgba(0, 0, 0, 0.2);
}

/* 日期输入框特殊样式 */
.filter-input[type="date"] {
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3crect x='3' y='4' width='18' height='18' rx='2' ry='2'%3e%3c/rect%3e%3cline x1='16' y1='2' x2='16' y2='6'%3e%3c/line%3e%3cline x1='8' y1='2' x2='8' y2='6'%3e%3c/line%3e%3cline x1='3' y1='10' x2='21' y2='10'%3e%3c/line%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 12px center;
  background-size: 16px;
  padding-right: 40px;
}

/* 数字输入框样式 */
.filter-input[type="number"] {
  appearance: textfield;
}

.filter-input[type="number"]::-webkit-inner-spin-button,
.filter-input[type="number"]::-webkit-outer-spin-button {
  appearance: none;
  margin: 0;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .filters-grid {
    grid-template-columns: 1fr;
    gap: 12px;
    padding: 16px;
  }
  
  .filters-header {
    padding: 12px 16px;
  }
  
  .filter-item-wide {
    grid-column: 1;
  }
}

@media (max-width: 480px) {
  .filters-header h3 {
    font-size: 15px;
  }
  
  .reset-btn {
    padding: 5px 10px;
    font-size: 12px;
  }
  
  .filter-input,
  .filter-select {
    padding: 8px 10px;
    font-size: 13px;
  }
  
  .filter-label {
    font-size: 12px;
  }
}

/* 高级筛选展开/收起 */
.advanced-filters-toggle {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 12px;
  background: rgba(0, 122, 255, 0.1);
  color: #007aff;
  border: none;
  border-radius: 6px;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  margin: 0 20px 16px;
}

.advanced-filters-toggle:hover {
  background: rgba(0, 122, 255, 0.2);
  transform: translateY(-1px);
}

.advanced-filters {
  background: rgba(0, 122, 255, 0.05);
  border-radius: 8px;
  margin: 0 20px 20px;
  padding: 16px;
  border: 1px solid rgba(0, 122, 255, 0.1);
}

/* 筛选按钮组 */
.filter-actions {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  padding: 16px 20px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  background: rgba(255, 255, 255, 0.4);
}

.filter-btn {
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.filter-btn-primary {
  background: #007aff;
  color: white;
}

.filter-btn-primary:hover {
  background: #0051d5;
  transform: translateY(-1px);
}

.filter-btn-secondary {
  background: white;
  color: #666;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.filter-btn-secondary:hover {
  background: #f5f5f7;
  transform: translateY(-1px);
}