/* HotlistEntriesStats 统计组件样式 */
.hotlist-entries-stats {
  background: rgba(255, 255, 255, 0.6);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  overflow: hidden;
}

.stats-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background: rgba(255, 255, 255, 0.4);
}

.stats-header h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
  color: #1d1d1f;
}

.stats-update-time {
  font-size: 12px;
  color: #999;
  font-weight: 500;
}

.stats-content {
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

/* 统计区块 */
.stat-section h4 {
  margin: 0 0 16px 0;
  font-size: 15px;
  font-weight: 600;
  color: #333;
}

/* 统计卡片 */
.stat-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
}

.stat-card {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 20px;
  background: white;
  border-radius: 12px;
  border: 1px solid rgba(0, 0, 0, 0.05);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  transition: all 0.2s ease;
}

.stat-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
}

.stat-card.total {
  background: linear-gradient(135deg, #007aff, #0051d5);
  color: white;
  border: none;
}

.stat-card.platforms {
  background: linear-gradient(135deg, #34c759, #30a14e);
  color: white;
  border: none;
}

.stat-icon {
  font-size: 24px;
  opacity: 0.9;
}

.stat-info {
  flex: 1;
}

.stat-number {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 4px;
  line-height: 1;
}

.stat-label {
  font-size: 13px;
  opacity: 0.8;
  font-weight: 500;
}

/* 平台排行 */
.platform-ranking {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.ranking-item {
  display: grid;
  grid-template-columns: 40px 1fr 120px;
  gap: 16px;
  align-items: center;
  padding: 12px 16px;
  background: white;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.05);
  transition: all 0.2s ease;
}

.ranking-item:hover {
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.ranking-position {
  display: flex;
  align-items: center;
  justify-content: center;
}

.rank-number {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background: #f5f5f7;
  color: #666;
  font-size: 14px;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ranking-item:nth-child(1) .rank-number {
  background: #ffd700;
  color: #333;
}

.ranking-item:nth-child(2) .rank-number {
  background: #c0c0c0;
  color: #333;
}

.ranking-item:nth-child(3) .rank-number {
  background: #cd7f32;
  color: white;
}

.ranking-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.platform-name {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 600;
  color: #333;
}

.platform-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  flex-shrink: 0;
}

.platform-count {
  font-size: 12px;
  color: #999;
  font-weight: 500;
}

.ranking-bar {
  height: 6px;
  background: #f5f5f7;
  border-radius: 3px;
  overflow: hidden;
}

.bar-fill {
  height: 100%;
  border-radius: 3px;
  transition: width 0.3s ease;
}

/* 详细统计表 */
.stats-table {
  background: white;
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.stats-table table {
  width: 100%;
  border-collapse: collapse;
}

.stats-table th,
.stats-table td {
  padding: 12px 16px;
  text-align: left;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.stats-table th {
  background: #f8f9fa;
  font-size: 13px;
  font-weight: 600;
  color: #666;
}

.stats-table td {
  font-size: 14px;
  color: #333;
}

.stats-table tr:hover {
  background: rgba(0, 122, 255, 0.02);
}

.rank-cell {
  font-weight: 600;
  color: #007aff;
  width: 60px;
}

.platform-cell {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 500;
}

.platform-indicator {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  flex-shrink: 0;
}

.count-cell {
  font-weight: 600;
  color: #333;
}

.percentage-cell {
  font-weight: 500;
  color: #666;
}

/* 加载状态 */
.hotlist-entries-stats.loading .stat-card {
  position: relative;
  overflow: hidden;
}

.loading-shimmer {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.6), transparent);
  animation: shimmer 1.5s infinite;
}

@keyframes shimmer {
  0% { left: -100%; }
  100% { left: 100%; }
}

/* 空状态 */
.hotlist-entries-stats.empty .stats-content {
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  text-align: center;
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
}

.empty-icon {
  font-size: 48px;
  opacity: 0.5;
}

.empty-state p {
  margin: 0;
  font-size: 14px;
  color: #999;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .stats-content {
    padding: 16px;
    gap: 20px;
  }
  
  .stat-cards {
    grid-template-columns: 1fr;
  }
  
  .ranking-item {
    grid-template-columns: 40px 1fr;
    gap: 12px;
  }
  
  .ranking-bar {
    grid-column: 1 / -1;
    order: 3;
  }
  
  .stats-table {
    overflow-x: auto;
  }
  
  .stats-table table {
    min-width: 400px;
  }
}

@media (max-width: 480px) {
  .stats-header {
    padding: 12px 16px;
    flex-direction: column;
    gap: 8px;
    align-items: flex-start;
  }
  
  .stats-header h3 {
    font-size: 15px;
  }
  
  .stats-update-time {
    font-size: 11px;
  }
  
  .stat-card {
    padding: 16px;
  }
  
  .stat-number {
    font-size: 20px;
  }
  
  .stat-label {
    font-size: 12px;
  }
  
  .stat-icon {
    font-size: 20px;
  }
}